body{min-height:100vh;margin:0;font-family:"Roboto",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:left;font-size:16px;color:#adc9e4}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}.spacing-top{margin-top:1rem}.text-center{text-align:center}

  *,
  ::before,
  ::after {
    box-sizing: border-box; 
  }

  html {
    line-height: 1.15; 
    -webkit-text-size-adjust: 100%; 
    scroll-behavior: smooth;
  }

  body, h1, h2, h3, h4, p, ul, li, a, label, span {
    margin: 0;
    padding: 0;
    text-decoration: none;
  }
  li {
    list-style-type: none;
  }
  a {
    text-decoration: none;
  }

  hr {
    box-sizing: content-box; 
    height: 0; 
    overflow: visible; 
  }

  pre {
    font-family: monospace, monospace; 
    font-size: 1em; 
  }

  abbr[title] {
    border-bottom: none; 
    text-decoration: underline; 
    -webkit-text-decoration: underline dotted; 
            text-decoration: underline dotted; 
  }

  b,
  strong {
    font-weight: bolder;
  }

  code,
  kbd,
  samp {
    font-family: monospace, monospace; 
    font-size: 1em; 
  }

  small {
    font-size: 80%;
  }

  sub,
  sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
  }
  sub {
    bottom: -0.25em;
  }
  sup {
    top: -0.5em;
  }

  img {
    border-style: none;
    display: block;
  }

  button,
  input,
  optgroup,
  select,
  textarea {
    font-family: inherit; 
    font-size: 100%; 
    line-height: 1.15; 
    margin: 0; 
  }

  button,
  input {
    
    overflow: visible;
  }

  button,
  select {
    
    text-transform: none;
  }

  button,
  [type='button'],
  [type='reset'],
  [type='submit'] {
    -webkit-appearance: button;
  }

  button::-moz-focus-inner,
  [type='button']::-moz-focus-inner,
  [type='reset']::-moz-focus-inner,
  [type='submit']::-moz-focus-inner {
    border-style: none;
    padding: 0;
  }

  fieldset {
    padding: 0.35em 0.75em 0.625em;
  }

  legend {
    box-sizing: border-box; 
    display: table; 
    max-width: 100%; 
    padding: 0; 
    white-space: normal; 
  }

  progress {
    vertical-align: baseline;
  }

  [type='number']::-webkit-inner-spin-button,
  [type='number']::-webkit-outer-spin-button {
    height: auto;
  }

  [type='search'] {
    -webkit-appearance: textfield; 
    outline-offset: -2px; 
  }

  [type='search']::-webkit-search-decoration {
    -webkit-appearance: none;
  }

  ::-webkit-file-upload-button {
    -webkit-appearance: button; 
    font: inherit; 
  }

  details {
    display: block;
  }

  summary {
    display: list-item;
  }
body {
    overflow-x: hidden;
    background-color: #001c31;
}

.navbar {
    display: flex;
    align-items: center;
    padding: 1rem 6rem;
    color: aliceblue;
    letter-spacing: 1px;
    justify-content: space-between;
    margin-right: 3rem;
    width: 100%;
    top: 0;
    left: 0;
    background: transparent;
    position: fixed;
    z-index: 100;
    transition: 0.2s;
}

.navbar.active {
    background: #002846;
}

.navbar ul {
    display: flex;
    align-items: center;
}

.navbar ul a {
    text-decoration: none;
}

.navbar ul li {
    color: #adc9e4;
    margin: 0 16px;
    text-transform: uppercase;
    padding: 10px;
    border-radius: 8px;
    transition: 0.3s;
    list-style: none;
}

.navbar ul li:hover {
    cursor: pointer;
    background: linear-gradient(70deg, #133fc2 0%, #2d70b2 50%, #28e4e9 100%);
    color: aliceblue;
    box-shadow: 0 6px 20px 0 rgb(0 0 0 / 60%);
}

.intro {
    margin: 36vh 46vw 0 14vw;
    color: #adc9e4;
}

.intro h1 {
    font-size: 70px;
    letter-spacing: 2px;
    font-weight: 700;
}

.intro p {
    font-weight: 100;
    font-size: 18px;
    margin-top: 20px;
    line-height: 2rem;
}

.intro button {
    text-transform: uppercase;
    padding: 10px 20px;
    border-radius: 1rem;
    border: none;
    background: linear-gradient(70deg, #133fc2 0%, #2d70b2 50%, #28e4e9 100%);
    margin-top: 50px;
    cursor: pointer;
    color: aliceblue;
    transition: 0.3s;
    font-size: 16px;
    height: 54px;
    width: 10rem;
    box-shadow: 0 6px 20px 0 rgb(0 0 0 / 60%);
}

.intro button:hover {
    background-position: center;
    box-shadow: 0 6px 20px 5px rgb(0 0 0 / 60%);
}

.intro img {
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    opacity: 0.7;
}

.bg {
    overflow-x: hidden;
    background: url(/static/media/bg.f0796761.jpg) no-repeat;
    background-size: cover;
    min-height: 100vh;
}

.logo {
    display: flex;
    align-items: center;
    font-size: 18px;
    color: #adc9e4;
}

.logo img {
    height: 50px;
    margin-right: 1rem;
}

.stt {
    height: 50px;
    width: 50px;
    background-color: #002846;
    bottom: 25px;
    right: 25px;
    position: fixed;
    border-radius: 50%;
    box-shadow: 0 6px 20px 0 rgb(0 0 0 / 60%);
    display: none;
    transition: 0.5s;
}

.stt svg {
    fill: #adc9e4;
    margin: 13px 0 0 13px;
    height: 18px;
}

.stt.active {
    display: inline;
}

.about {
    padding: 15vh 20vw 15vh 20vw;
    min-height: 40vh;
}

.about .title, .pros {
    text-align: center;
}

.about h1 {
    letter-spacing: 1px;
    font-weight: 400;
    font-size: 40px;
}

.about p {
    font-size: 18px;
    margin-top: 12px;
    font-weight: 200;
}

.about-content p {
    margin-top: 2rem;
}

.about-content {
    margin-top: 4rem;
    line-height: 2rem;
}

.landing {
    padding-top: 2rem;
    width: 100%;
    min-height: 32vh;
    background: linear-gradient(70deg, #133fc2 0%, #2d70b2 50%, #28e4e9 100%);
}

.pros {
    padding: 0 auto;
    position: relative;
}

.pros h1 {
    color: aliceblue;
    font-weight: 200;
    letter-spacing: 1px;
}

.pros-content {
    align-items: center;
    justify-content: center;
    display: flex;
}

.pros-content svg {
    fill: aliceblue;
}

.cont svg {
    height: 70px;
    margin-bottom: 10px;
}

.cont {
    margin: 2rem;
    width: 16rem;
    height: 10rem;
    border-radius: 8px;
    background: rgba(0,40,70,0.6);
    display: inline;
    color: aliceblue;
    padding-top: 24px;
}

.move {
    margin-left: 2rem;
}

.donate {
    background: linear-gradient(to bottom, rgba(0,28,49,0.2) 0%, rgba(0,40,70,0.05) 100%), url(/static/media/space.fab947f7.jpg) no-repeat top center;
    background-size: 100% auto;
    min-height: 100vh;
    padding-top: 1rem;
}

.donate button {
    margin: 3rem 3rem 0 0;
}

footer {
    border-top: solid 1px #2c4c65;
    padding: 25px 15rem;
}

.footer-content {
    display: flex;
    padding: 40px 0;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}

.footer-content .title {
  position: relative;
  font-weight: bold;
  font-size: 14px;
  color: #e6e9ec;
  line-height: 32px;
  margin-bottom: 26px;
  letter-spacing: 1px;
  text-align: left;
}

.footer-content .title:after {
  content: '';
  bottom: -2px;
  width: 50px;
  height: 2px;
  position: absolute;
  left: 0;
  background: linear-gradient(to right, #303395 0%, #27f0f0 100%);
}

.footer-content .footer-links {
  color: aliceblue;
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-content .footer-links li {
  padding: 0;
  margin: 0 0 24px 0;
}

.footer-content .footer-links a {
  color: #adc9e4;
  font-weight: 300;
  transition: 0.3s;
}

.footer-content .footer-links a:hover {
  color: #649cd1;
  font-weight: 300;
}

.copyright {
    text-align: left;
    font-weight: 200;
    font-size: 14px;
}

.footer-content .dev {
    max-width: 420px;
    font-weight: 200;
}

.footer-content .dev p {
    line-height: 1.5rem;
}

.footer-content .contact {
  color: #adc9e4;
  font-weight: 300;
}

.footer-content .contact li {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.footer-content .contact img {
    height: 25px;
    margin-right: 10px;
}
